<template>
  <div v-bind:style="{ backgroundColor: activeColor }" class="hello">
    <form action="none">
      <label for="time">Uhrzeit von: </label>
      <input v-model="time" id="time" placeholder="Uhrzeit" type="time" />

      <label for="end">bis: </label>
      <input v-model="end" id="end" type="time" />

      <select v-model="selected">
        <option disabled value="">Please select one</option>
        <option>Karamini</option>
        <option>Kinder & Jugend</option>
        <option>Kinder A</option>
        <option>Erwachsene</option>
        <option disabled value="">----------</option>
        <option>Karate & Selbstverteidigung</option>
        <option disabled value="">----------</option>
        <option>Benutzerdefiniert</option>
      </select>
    </form>
    <input
      v-model="customText"
      v-if="selected == 'Benutzerdefiniert'"
      placeholder="Text..."
    />
    <button v-on:click="updateData">
      Speichern (Day {{ day }}, Einheit {{ session }})
    </button>
  </div>
</template>

<script>
import { doc, updateDoc, getFirestore, setDoc } from "firebase/firestore";
import { initializeApp } from "@firebase/app";

initializeApp({
  apiKey: "AIzaSyAd5SQiFe5RRm954rTLRnEPPGEYznSMSBo",
  authDomain: "shintaikan-6b670.firebaseapp.com",
  databaseURL: "https://shintaikan-6b670.firebaseio.com",
  projectId: "shintaikan-6b670",
  storageBucket: "shintaikan-6b670.appspot.com",
  messagingSenderId: "929946590509",
  appId: "1:929946590509:web:7107bf53389ab56dc507a4",
});
const db = getFirestore();
export default {
  name: "HelloWorld",
  props: {
    day: Number,
    session: Number,
    data: Object,
  },
  data() {
    return {
      selected: this.data.group,
      activeColor: "transparent",
      time: this.data.start,
      end: this.data.end,
      customText: this.data.customText,
    };
  },
  methods: {
    async updateData() {
      let that = this;
      await updateDoc(doc(db, "times", `${that.day}${that.session}`), {
        start: this.time,
        end: this.end,
        group: this.selected,
        customText: this.customText,
      }).catch(function () {
        setDoc(doc(db, "times", `${that.day}${that.session}`), {
          start: that.time,
          end: that.end,
          group: that.selected,
          customText: that.customText,
        });
      });
      this.activeColor = "#8BFF77";
    },
  },
};
//console.log(this.data);
</script>

